import { useRouter } from "vue-router";

export default function PageChangeHandler() {
  const router = useRouter();

  const goPage = async (_pageName) => {
    try {
      await router.push({ name: _pageName }); // 跳转到指定页面
    } catch (error) {
      console.error("Page navigation failed", error);
    }
  };

  return {
    goPage,
  };
}
