import { Notify } from "vant";
import Web3 from "web3";
import { ref } from "vue";

// 创建一个 Web3 实例
let web3;
if (window.ethereum) {
  web3 = new Web3(window.ethereum);
} else {
  console.error("MetaMask is not installed.");
}

export default function useTokenAFunctions(tokenAContract, TokenA_Account) {
  const tokenBanlance = ref(0); // 用于显示余额
  const tokenApprove = ref(0);
  const totalSupply = ref(0);

  // 授权函数
  const Approve = async (_approveAmount) => {
    const targetAddress = "0xF8eA5efA375E6E44A6D01307f413a00b84e48B10"; // 不需要使用 ref
    const accountAddress = await TokenA_Account(); // 使用 TokenA_Account 获取账户地址

    const approveAmount = Web3.utils
      .toBN(_approveAmount)
      .mul(Web3.utils.toBN(10).pow(Web3.utils.toBN(18)));

    try {
      const result = await tokenAContract.methods
        .approve(targetAddress, approveAmount)
        .send({ from: accountAddress });
      Notify({ type: "success", message: "授权成功" });
    } catch (error) {
      Notify({ type: "danger", message: "授权失败: " + error.message });
    }
  };

  // 铸造函数
  const Mint = async (_mintAmount) => {
    const accountAddress = await TokenA_Account();

    console.log(_mintAmount);

    // 确保 _mintAmount 是数字类型，然后乘以 10 的 18 次方
    const mintAmountInWei = Web3.utils
      .toBN(_mintAmount)
      .mul(Web3.utils.toBN(10).pow(Web3.utils.toBN(18)));

    console.log(mintAmountInWei.toString());

    try {
      const result = await tokenAContract.methods
        .mint(mintAmountInWei.toString())
        .send({ from: accountAddress });
    } catch (error) {
      console.error(error);
      alert("铸造失败");
    }
  };

  // 获取余额函数
  const GetBalance = async () => {
    const accountAddress = await TokenA_Account();
    try {
      const balance = await tokenAContract.methods
        .balanceOf(accountAddress)
        .call();

      // 使用 web3 来转换余额
      const balanceInEther = web3.utils.fromWei(balance, "ether"); // 转换为以太单位
      tokenBanlance.value = balanceInEther; // 更新响应式变量

      // 通过 Vue 数据绑定来显示余额，不需要直接操作 DOM
      // document.getElementById("balance").innerText = `Balance: ${balanceInEther} TKA`;
    } catch (error) {
      console.error(error);
      alert("Error fetching balance.");
    }
  };

  // 获取授权代币
  const GetApprove = async () => {
    console.log("GetApprove called");
    const accountAddress = await TokenA_Account();
    const targetAddress = "0xF8eA5efA375E6E44A6D01307f413a00b84e48B10";

    try {
      const balance = await tokenAContract.methods
        .allowance(accountAddress, targetAddress)
        .call();

      // 使用 web3 来转换余额
      const balanceInEther = web3.utils.fromWei(balance, "ether"); // 转换为以太单位
      tokenApprove.value = balanceInEther; // 更新响应式变量
    } catch (error) {
      console.error(error);
      alert("Error fetching approve amount.");
    }
  };
  // 总供应量
  const GetTotalSupply = async () => {
    try {
      const balance = await tokenAContract.methods.totalSupply().call();

      // 使用 web3 来转换余额
      const balanceInEther = web3.utils.fromWei(balance, "ether"); // 转换为以太单位
      totalSupply.value = balanceInEther; // 更新响应式变量
    } catch (error) {
      console.error(error);
      alert("Error fetching query amount.");
    }
  };

  return {
    totalSupply,
    tokenApprove,
    tokenBanlance,
    Approve,
    Mint,
    GetBalance,
    GetApprove,
    GetTotalSupply,
  };
}
