import { ref } from "vue";
import { Notify } from "vant";
import Web3 from "web3";

export default function useTokenLockFunctions(
  tokenLockContract,
  getTokenLockAccount
) {
  // 响应式数据
  const originAmount = ref(0);
  const totalTokenAmount = ref(0);
  const leftUnlockTime = ref(0);
  const rewardAmount = ref(0);
  const punishmentAmount = ref(0);
  const blockTime = ref(0);

  // 锁定代币
  const LockTokens = async (_tokenLockAmount, _unLockTime) => {
    const tokenLockAddress = await getTokenLockAccount();
    const tokenLockAmount = _tokenLockAmount;

    try {
      await tokenLockContract.methods
        .lockTokens(tokenLockAmount, _unLockTime)
        .send({ from: tokenLockAddress });

      Notify({ type: "success", message: "锁定成功" });
    } catch (error) {
      Notify({ type: "danger", message: "锁定失败: " + error.message });
    }
  };

  // 解锁代币
  const UnLockTokens = async () => {
    const tokenLockAddress = await getTokenLockAccount();
    try {
      await tokenLockContract.methods
        .unlockTokens()
        .send({ from: tokenLockAddress });

      Notify({ type: "success", message: "解锁成功" });
    } catch (error) {
      Notify({ type: "danger", message: "解锁失败: " + error.message });
    }
  };

  // 强制解锁代币
  const ForceUnLockTokens = async () => {
    const tokenLockAddress = await getTokenLockAccount();
    try {
      await tokenLockContract.methods
        .forceUnLockTokens()
        .send({ from: tokenLockAddress });

      Notify({ type: "success", message: "强制解锁成功" });
    } catch (error) {
      Notify({ type: "danger", message: "强制解锁失败: " + error.message });
    }
  };

  // 获取存取信息
  const GetLockedTokens = async () => {
    const tokenLockAddress = await getTokenLockAccount();
    try {
      const result = await tokenLockContract.methods
        .lockedTokens(tokenLockAddress) // 调用 getLockedTokens 方法
        .call();

      if (
        result &&
        result.originAmount &&
        result.tokenAmount &&
        result.unlockTime
      ) {
        originAmount.value = Web3.utils.fromWei(result.originAmount, "ether"); // 将Wei转换为Ether
        totalTokenAmount.value = Web3.utils.fromWei(
          result.tokenAmount,
          "ether"
        ); // 将Wei转换为Ether
        leftUnlockTime.value = result.unlockTime; // 直接赋值时间戳

        Notify({ type: "success", message: "查看锁定信息成功" });
      } else {
        Notify({ type: "danger", message: "未能获取有效的锁定数据" });
      }
    } catch (error) {
      Notify({ type: "danger", message: "获取锁定信息失败: " + error.message });
    }
  };

  // 获取区块链时间
  const GetBlockTime = async () => {
    const tokenLockAddress = await getTokenLockAccount();
    try {
      const result = await tokenLockContract.methods
        .getBlockTimestamp()
        .call({ from: tokenLockAddress });
      blockTime.value = result;
    } catch (error) {
      Notify({
        type: "danger",
        message: "获取区块时间失败: " + error.message,
      });
    }
  };
  // 获取奖励和惩罚金额
  const GetRewardAndPunishment = async () => {
    const tokenLockAddress = await getTokenLockAccount();
    try {
      const result = await tokenLockContract.methods
        .getRewardAndPunishment()
        .call({ from: tokenLockAddress });

      if (result && result[0] && result[1]) {
        rewardAmount.value = Web3.utils.fromWei(result[0], "ether"); // 转换为 Ether 单位
        punishmentAmount.value = Web3.utils.fromWei(result[1], "ether"); // 转换为 Ether 单位

        Notify({ type: "success", message: "获取奖励和惩罚金额成功" });
      } else {
        Notify({ type: "danger", message: "未能获取有效的奖励和惩罚数据" });
      }
    } catch (error) {
      Notify({
        type: "danger",
        message: "获取奖励和惩罚金额失败: " + error.message,
      });
    }
  };

  // 返回响应式数据和函数
  return {
    blockTime,
    originAmount,
    totalTokenAmount,
    leftUnlockTime,
    rewardAmount,
    punishmentAmount,
    LockTokens,
    UnLockTokens,
    ForceUnLockTokens,
    GetLockedTokens,
    GetRewardAndPunishment,
    GetBlockTime,
  };
}
