import Web3 from "web3";
import tokenLockJSON from "../contract/tokenLock.json";

const useTokenLock = () => {
  const web3 = new Web3(
    Web3.givenProvider ||
      "wss://sepolia.infura.io/ws/v3/b5ec59d9451e4bdeaf5b81d6e87277ee"
  );

  const contractAddress = "0xF8eA5efA375E6E44A6D01307f413a00b84e48B10";
  const tokenLockContract = new web3.eth.Contract(
    tokenLockJSON.abi,
    contractAddress
  );

  const getTokenLockAccount = async () => {
    try {
      const accounts = await web3.eth.requestAccounts();
      return accounts[0];
    } catch (error) {
      console.error("Error fetching accounts: ", error);
      return null;
    }
  };

  return {
    web3,
    tokenLockContract,
    contractAddress,
    getTokenLockAccount,
  };
};

export default useTokenLock;
