import { createRouter, createWebHistory } from "vue-router";

import MainPage from "../views/MainPage.vue";
import LockTokenPage from "@/views/LockTokenPage.vue";
import TokenAPage from "@/views/TokenAPage.vue";
import UnLockTokenPage from "@/views/UnLockTokenPage.vue";

const routes = [
  {
    path: "/",
    name: "HOME",
    component: MainPage,
  },
  {
    path: "/LockTokenPage",
    name: "LockToken",
    component: LockTokenPage,
  },
  {
    path: "/TokenAPage",
    name: "MINT TOKEN",
    component: TokenAPage,
  },
  {
    path: "/UnLockTokenPage",
    name: "UnLockToken",
    component: UnLockTokenPage,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
