import Web3 from "web3";
import tokenAJSON from "../contract/tokenA.json";

const useTokenA = () => {
  // 初始化 Web3 实例
  const web3 = new Web3(
    Web3.givenProvider ||
      "wss://sepolia.infura.io/ws/v3/b5ec59d9451e4bdeaf5b81d6e87277ee"
  );
  console.log("Web3 instance: ", web3);

  // 合约地址和实例化合约
  const contractAddress = "0x56250Af156a91d147DcB0B0db9B18010cC4A5B98";
  const tokenAContract = new web3.eth.Contract(tokenAJSON.abi, contractAddress);
  console.log("TokenA contract instance: ", tokenAContract);

  // 获取当前账户
  const getTokenA_Account = async () => {
    try {
      const accounts = await web3.eth.requestAccounts();
      return accounts[0];
    } catch (error) {
      console.error("Error fetching accounts: ", error);
      return null;
    }
  };

  return {
    web3,
    tokenAContract,
    contractAddress,
    getTokenA_Account,
  };
};

export default useTokenA;
